.form {
  position: relative;
  display: flex;
  align-items: center;
  gap: 3rem;
  justify-content: flex-end;
  max-width: 100%;

  @media (min-width: $stop-scaling) {
    gap: 30px;
  }

  @media (orientation: portrait) {
    gap: 1rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    gap: 1rem;
  }
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-bottom: 12px;
    font-size: 16px;
  }

  @media (orientation: portrait) {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field-wrap {
  width: 100%;
  text-align: right;
}

.form__field {
  padding: 0 5.6rem;
  height: 6rem;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-dark;
  border: 0;
  border-radius: 3rem;
  outline: none;
  position: relative;
  opacity: 0;
  width: 0%;

  &:focus {
    border: 1px solid $c-purple;
  }

  @media (min-width: $stop-scaling) {
    padding: 0 56px;
    height: 60px;
    width: calc(100% - 210px);
    font-size: 24px;
    border-radius: 30px;
  }

  @media (orientation: portrait) {
    padding: 0 2.5rem;
    width: 0;
    height: 4rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    width: calc(100% - 9rem);
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 0 2.5rem;
    width: 0;
    height: 4rem;
    font-size: 1.6rem;
  }

  &::placeholder {
    opacity: 0;
  }
}


