}

.game__body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.game__buttons {
  position: fixed;
  left: 0;
  z-index: 100;
  opacity: 0.5;
  display: block;
}

.game__button {
  margin: 0 30px 30px;
  display: block;
}

