  }
  100% {
    transform: scale(1);
    transform-origin: 0% 100%;
  }
}
@keyframes scale-out-left {
  0% {
    transform: scale(1);
    transform-origin: 0% 100%;
  }
  100% {
    transform: scale(0);
    transform-origin: 0% 100%;
  }
}
